.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* components */
.title {
  font: 400 3.6rem/1 "Oswald", "Arial", sans-serif;
}
@media (min-width: 769px) {
  .title {
    font: 400 6rem/1 "Oswald", "Arial", sans-serif;
  }
}
@media (min-width: 1440px) {
  .title {
    font: 400 6rem/1 "Oswald", "Arial", sans-serif;
  }
}

.title-medium {
  margin: 0;
  padding: 0;
  font: 400 2.4rem/1.08 "Oswald", "Arial", sans-serif;
}
@media (min-width: 769px) {
  .title-medium {
    font: 400 3.6rem/normal "Oswald", "Arial", sans-serif;
  }
}
@media (min-width: 1440px) {
  .title-medium {
    font: 400 3.6rem/normal "Oswald", "Arial", sans-serif;
  }
}

.button {
  text-decoration: none;
  color: #000000;
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  color: #ffffff;
  font: 400 1.6rem/1.25 "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-align: center;
  background-color: #68b738;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--grey {
  background-color: #f2f2f2;
  color: #444444;
}
.button--grey:hover {
  background-color: #ebebeb;
}
.button--grey:active {
  color: #444444;
}

.description {
  margin: 0;
  padding: 0 0 0 20px;
  width: 250px;
  font: 400 1.4rem/1.28 "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
}
@media (min-width: 769px) {
  .description {
    font: 400 2rem/1.5 "Oswald", "Arial", sans-serif;
  }
}
@media (min-width: 1440px) {
  .description {
    font: 400 2rem/1.5 "Oswald", "Arial", sans-serif;
  }
}

.text-input {
  margin: 0;
  padding: 0 20px;
  position: relative;
  display: flex;
  column-gap: 14px;
}
.text-input:last-child {
  margin: 0;
}
@media (min-width: 769px) {
  .text-input {
    padding: 0 70px;
  }
}
@media (min-width: 1440px) {
  .text-input {
    padding: 0;
  }
}

.text-input__label {
  display: block;
  padding: 14px 0 0;
  font: 400 1.6rem/1.12 "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: #444444;
  text-wrap: nowrap;
}
.text-input__label:hover {
  cursor: pointer;
}
@media (min-width: 769px) {
  .text-input__label {
    padding: 10px 0 0;
    font: 400 2rem/normal "Oswald", "Arial", sans-serif;
  }
}
@media (min-width: 1440px) {
  .text-input__label {
    font: 400 2rem/normal "Oswald", "Arial", sans-serif;
  }
}

.text-input__input {
  margin: 0;
  padding: 0 40px 0 14px;
  border: 1px solid #e7e7e7;
  width: 100%;
  height: 45px;
  font: 400 1.6rem/1.12 "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: #444444;
}
.text-input__input::placeholder {
  color: inherit;
}
.text-input__input:hover {
  border-color: #cdcdcd;
}
.text-input__input:focus {
  outline: 2px solid #444444;
  outline-offset: -2px;
  border: 1px solid transparent;
}
.text-input__input:invalid {
  outline: 2px solid #ff8282;
  outline-offset: -2px;
  border: 1px solid transparent;
}
@media (min-width: 769px) {
  .text-input__input {
    padding: 0 45px 0 19px;
    height: 52px;
    font: 400 2rem/normal "Oswald", "Arial", sans-serif;
  }
}

.text-input__icon {
  display: block;
  position: absolute;
  top: 15px;
  right: 34px;
}
@media (min-width: 769px) {
  .text-input__icon {
    top: 18px;
    right: 85px;
  }
}
@media (min-width: 1440px) {
  .text-input__icon {
    top: 19px;
    right: 16px;
  }
}

.radio-input {
  padding: 0 20px;
  position: relative;
  min-height: 30px;
}
@media (min-width: 769px) {
  .radio-input {
    padding: 0;
    min-height: 40px;
  }
}

.radio-input__label {
  display: block;
  padding: 7px 0 0 45px;
  position: relative;
  font: 400 1.6rem/1.12 "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: #444444;
}
.radio-input__label::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  width: 30px;
  height: 30px;
}
@media (min-width: 769px) {
  .radio-input__label::before {
    width: 40px;
    height: 40px;
    top: -5px;
    left: -1px;
  }
}
@media (min-width: 1440px) {
  .radio-input__label::before {
    width: 40px;
    height: 40px;
    top: -3px;
    left: 0;
  }
}
.radio-input__label::after {
  content: "";
  display: none;
  border-radius: 50%;
  position: absolute;
  top: 8px;
  left: 8px;
  width: 13px;
  height: 13px;
  background-color: #68b738;
}
@media (min-width: 769px) {
  .radio-input__label::after {
    width: 18px;
    height: 18px;
    top: 6px;
    left: 10px;
  }
}
@media (min-width: 1440px) {
  .radio-input__label::after {
    top: 8px;
    left: 11px;
  }
}
.radio-input__label:hover {
  cursor: pointer;
}
.radio-input__label:hover::before {
  border-color: #bababa;
}
@media (min-width: 769px) {
  .radio-input__label {
    padding: 0 0 0 59px;
    font: 400 2rem/normal "Oswald", "Arial", sans-serif;
  }
}
@media (min-width: 1440px) {
  .radio-input__label {
    padding: 2px 0 0 59px;
  }
}

.radio-input__label[disabled] {
  pointer-events: none;
  opacity: 0.3;
}

.radio-input__input:checked + .radio-input__label::after {
  display: block;
}

.textarea {
  padding: 14px 13px 14px 14px;
  border: 1px solid #e7e7e7;
  width: calc(100% - 40px);
  height: 150px;
  font: 400 1.6rem/1.12 "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: #444444;
  resize: none;
}
.textarea::placeholder {
  color: inherit;
}
.textarea:hover {
  border-color: #cdcdcd;
}
.textarea:focus {
  outline: 2px solid #444444;
  outline-offset: -2px;
  border: 1px solid transparent;
}
.textarea:invalid {
  outline: 2px solid #ff8282;
  outline-offset: -2px;
  border: 1px solid transparent;
}
@media (min-width: 769px) {
  .textarea {
    padding: 10px 13px 14px 19px;
    width: calc(100% - 140px);
    height: 175px;
    font: 400 2rem/normal "Oswald", "Arial", sans-serif;
  }
}
@media (min-width: 1440px) {
  .textarea {
    padding: 8px 13px 14px 19px;
    display: block;
    width: 1220px;
  }
}

.checkbox-input {
  padding: 0 20px;
}
@media (min-width: 769px) {
  .checkbox-input {
    padding: 0;
  }
}

.checkbox-input__label {
  display: block;
  padding: 7px 0 0 45px;
  font: 400 1.6rem/1.12 "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  color: #444444;
  position: relative;
}
.checkbox-input__label::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #cdcdcd;
}
@media (min-width: 769px) {
  .checkbox-input__label::before {
    width: 40px;
    height: 40px;
  }
}
@media (min-width: 1440px) {
  .checkbox-input__label::before {
    top: -1px;
  }
}
.checkbox-input__label::after {
  content: "";
  display: none;
  width: 26px;
  height: 26px;
  background: url("../img/checkbox-tick.svg") no-repeat center;
  position: absolute;
  top: 3px;
  left: 2px;
}
@media (min-width: 769px) {
  .checkbox-input__label::after {
    background-size: 48px 36px;
    top: 7px;
    left: 7px;
  }
}
@media (min-width: 1440px) {
  .checkbox-input__label::after {
    top: 6px;
  }
}
.checkbox-input__label:hover {
  cursor: pointer;
}
.checkbox-input__label:hover::before {
  border-color: #bababa;
}
@media (min-width: 769px) {
  .checkbox-input__label {
    padding: 6px 0 0 60px;
    font: 400 2rem/normal "Oswald", "Arial", sans-serif;
  }
}
@media (min-width: 769px) {
  .checkbox-input__label {
    padding: 5px 0 0 60px;
  }
}

.checkbox-input__label[disabled] {
  pointer-events: none;
  opacity: 0.3;
}

.checkbox-input__input:checked + .checkbox-input__label::after {
  display: block;
}

.catalog-card {
  padding: 22px 20px 24px;
  border-top: 1px solid #ebebeb;
  display: grid;
  grid-template-columns: repeat(2, 2fr);
  grid-template-rows: auto auto;
  row-gap: 20px;
}
@media (min-width: 769px) {
  .catalog-card {
    padding: 0 37px 40px;
    grid-template-columns: auto;
    grid-template-rows: 139px auto min-content;
    row-gap: 22px;
    width: 324px;
    min-height: 430px;
    background-color: #f2f2f2;
  }
}
@media (min-width: 1440px) {
  .catalog-card {
    padding: 0 38px 40px;
    width: 245px;
    min-height: 430px;
    row-gap: 12px;
  }
}

.catalog-card__image {
