.main-nav {
  position: relative;
  z-index: 3;
}

.main-nav__toggle {
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  position: absolute;
  top: -43px;
  right: 21px;
  width: 24px;
  height: 19px;
}
.main-nav__toggle:hover {
  opacity: 0.8;
}
.main-nav__toggle:active {
  opacity: 0.6;
}
@media (min-width: 769px) {
  .main-nav__toggle {
    display: none;
  }
