.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 0 36px;
  margin-left: auto;
  padding: 0;
}

/* закрытое меню */
.main-nav--closed .main-nav__toggle::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0px 8px 0 0 #000000, 0px 16px 0 0 #000000;
